import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-form2',
  templateUrl: './form2.component.html',
  styleUrls: ['./form2.component.css']
})
export class Form2Component implements OnInit {
  formStep: number = 1; // Default to the first step
  totalSteps: number = 5; // Total number of steps in your form

  form: FormGroup;

  constructor(private fb: FormBuilder, private router: Router, private route: ActivatedRoute) {
    // Initialize forms for each step
    this.form = this.fb.group({
      step1: this.fb.group({ checkinDate: [''], checkoutDate: [''] }),
      step2: this.fb.group({ hotelName: [''], hotelLocation: [''] }),
      step3: this.fb.group({ roomType: [''], numberOfRooms: [1] }),
      step4: this.fb.group({ destination: [''] }),
      step5: this.fb.group({ paymentMethod: [''], cardDetails: [''] })
    });
  }

  ngOnInit(): void {
    this.route.firstChild?.params.subscribe(params => {
      const step = params['step'];
      if (step) {
        this.formStep = this.getStepNumber(step);
      }
    });
  }

  getCurrentForm(): FormGroup {
    switch (this.formStep) {
      case 1: return this.form.get('step1') as FormGroup;
      case 2: return this.form.get('step2') as FormGroup;
      case 3: return this.form.get('step3') as FormGroup;
      case 4: return this.form.get('step4') as FormGroup;
      case 5: return this.form.get('step5') as FormGroup;
      default: return this.form.get('step1') as FormGroup;
    }
  }

  nextStep(): void {
    if (this.formStep < this.totalSteps) {
      this.formStep++;
      this.router.navigate([`/form2/${this.getStepPath(this.formStep)}`]);
    }
  }

  prevStep(): void {
    if (this.formStep > 1) {
      this.formStep--;
      this.router.navigate([`/form2/${this.getStepPath(this.formStep)}`]);
    }
  }

  getStepPath(step: number): string {
    switch (step) {
      case 1: return 'date';
      case 2: return 'hotel_list';
      case 3: return 'rooms';
      case 4: return 'destination';
      case 5: return 'payment';
      default: return 'date';
    }
  }

  getStepNumber(path: string): number {
    switch (path) {
      case 'date': return 1;
      case 'hotel_list': return 2;
      case 'rooms': return 3;
      case 'destination': return 4;
      case 'payment': return 5;
      default: return 1;
    }
  }

  getProgress(): string {
    return `${(this.formStep / this.totalSteps) * 100}%`;
  }

  submit(): void {
    if (this.form.valid) {
      console.log('Form submitted successfully:', this.form.value);
      // Add form submission logic here
    } else {
      console.error('Form is invalid');
    }
  }
}
