import { NgModule } from '@angular/core';
import { FormsModule} from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { MatInputModule } from '@angular/material/input';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { BrowserModule, provideClientHydration } from '@angular/platform-browser';
import { AppRoutingModule } from './app-routing.module';
import { NgxDaterangepickerMd } from 'ngx-daterangepicker-material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { defineLocale } from 'ngx-bootstrap/chronos';
import { enGbLocale } from 'ngx-bootstrap/locale';
import { NgxDropzoneModule } from 'ngx-dropzone';
import { NgxEditorModule } from 'ngx-editor';
import { BaseChartDirective } from 'ng2-charts';
import { provideCharts, withDefaultRegisterables } from 'ng2-charts';
import { AngularEditorModule } from '@kolkov/angular-editor';
import { MatCheckboxModule } from '@angular/material/checkbox'; 
import { QuillModule } from 'ngx-quill'

defineLocale('en-gb', enGbLocale);



import { AppComponent } from './app.component';
import { AboutComponent } from './about/about.component';
import { BlogComponent } from './blog/blog.component';
import { ContactComponent } from './contact/contact.component';
import { GalleryComponent } from './gallery/gallery.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { Form2Component } from './form2/form2.component';
import { BookingFormComponent } from './booking-form/booking-form.component';
import { DestinationComponent } from './form2/destination/destination.component';
import { DateComponent } from './form2/date/date.component';
import { RoomsComponent } from './form2/rooms/rooms.component';
import { HotelListComponent } from './form2/hotel-list/hotel-list.component';
import { PaymentComponent } from './form2/payment/payment.component';
import { HomeComponent } from './home/home.component';
import { SigninComponent } from './signin/signin.component';
import { LoginComponent } from './login/login.component';
import { GraphComponent } from './graph/graph.component';
import { TexteditorComponent } from './texteditor/texteditor.component';
import { HotelList2Component } from './form2/hotel-list2/hotel-list2.component';
import { HotelList3Component } from './form2/hotel-list3/hotel-list3.component';
import { HotelList4Component } from './form2/hotel-list4/hotel-list4.component';
import { HotelList5Component } from './form2/hotel-list5/hotel-list5.component';
import { FormReceiptComponent } from './form-receipt/form-receipt.component';
import { PdfComponent } from './form2/pdf/pdf.component';



declare const Quill: any;

@NgModule({
  declarations: [
    AppComponent,
    AboutComponent,
    BlogComponent,
    ContactComponent,
    GalleryComponent,
    HeaderComponent,
    FooterComponent,
    Form2Component,
    BookingFormComponent,
    DestinationComponent,
    DateComponent,
    RoomsComponent,
    HotelListComponent,
    PaymentComponent,
    HomeComponent,
    SigninComponent,
    LoginComponent,
    GraphComponent,
    TexteditorComponent,
    HotelList2Component,
    HotelList3Component,
    HotelList4Component,
    HotelList5Component,
    FormReceiptComponent,
    PdfComponent,

  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    ReactiveFormsModule,
    NgbModule,
    FormsModule,
    MatInputModule,
    MatNativeDateModule,
    MatDatepickerModule,
    BrowserAnimationsModule,
    MatFormFieldModule,
    MatCheckboxModule,
    MatButtonModule,
    AngularEditorModule,
    MatIconModule,
    BaseChartDirective,
    NgxDropzoneModule,
    QuillModule.forRoot(),
    NgxEditorModule.forRoot({
      locals: {
        bold: 'Bold',
        italic: 'Italic',
        code: 'Code',
        underline: 'Underline',
        // Add more localized button names as needed
      },
    }),
    NgxDaterangepickerMd.forRoot(),
    BsDatepickerModule.forRoot()
  ],
  providers: [
    provideClientHydration(),
    provideCharts(withDefaultRegisterables())
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }