import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';

interface Room {
  adults: number;
  children: number;
  additionalCharges: { [key: number]: boolean }; // Object to keep track of additional charges
}

interface Interest {
  id: number;
  name: string;
  price: number;
}

@Component({
  selector: 'app-rooms',
  templateUrl: './rooms.component.html',
  styleUrls: ['./rooms.component.css']
})
export class RoomsComponent implements OnInit {
  rooms: Room[] = [];
  interests: Interest[] = [];
  selectedPrice: number = 2000; // Example default price per room
  totalPrice: number = 0;

  adultsOptions = [1, 2];
  childrenOptions = [0, 1, 2];

  constructor(private router: Router) {}

  ngOnInit() {
    this.addRoom(); // Initialize with one room
    this.interests = [
      { id: 1, name: 'Breakfast', price: 500 },
      { id: 2, name: 'Parking', price: 300 },
      { id: 3, name: 'WiFi', price: 200 }
    ];
    this.calculateTotalPrice();
  }

  addRoom() {
    this.rooms.push({
      adults: 1,
      children: 0,
      additionalCharges: this.interests.reduce((acc, interest) => {
        acc[interest.id] = false;
        return acc;
      }, {} as { [key: number]: boolean })
    });
    this.calculateTotalPrice();
  }

  removeRoom(index: number) {
    this.rooms.splice(index, 1);
    this.calculateTotalPrice();
  }

  updateRoomInterest(roomIndex: number, interestId: number, isChecked: boolean) {
    this.rooms[roomIndex].additionalCharges[interestId] = isChecked;
    this.calculateTotalPrice();
  }

  calculateTotalPricePerRoom(room: Room): number {
    let additionalChargesTotal = 0;
    for (const interest of this.interests) {
      if (room.additionalCharges[interest.id]) {
        additionalChargesTotal += interest.price;
      }
    }
    return this.selectedPrice + additionalChargesTotal;
  }

  calculateTotalPrice() {
    this.totalPrice = this.rooms.reduce((total, room) => {
      return total + this.calculateTotalPricePerRoom(room);
    }, 0);
  }

  getMinimumRoomsRequired(): number {
    return 1; // Adjust based on your business logic
  }

  isFormValid(): boolean {
    // Check if each room has adults selected, and if each room has at least one additional charge selected or no additional charges
    return this.rooms.every(room => room.adults > 0 && room.children >= 0) &&
           this.rooms.every(room => Object.values(room.additionalCharges).some(isChecked => isChecked));
  }

  submitRoomForm(form: NgForm) {
    if (form.valid && this.isFormValid()) {
      console.log('Form Submitted!', this.rooms);
      this.router.navigate(['/form2/destination']);
    } else {
      console.log('Form is invalid or incomplete.');
      // Handle form submission logic here
    }
  }
}
