import { Component, OnInit } from '@angular/core';
import { Chart, registerables } from 'chart.js';
Chart.register(...registerables);

@Component({
  selector: 'app-graph',
  templateUrl: './graph.component.html',
  styleUrls: ['./graph.component.css']
})
export class GraphComponent implements OnInit {
  constructor() { }
  ngOnInit(): void {
    this.RenderChart();
  }

  RenderChart() {
    new Chart("barchart", {
      type: 'line',
      data: {
        labels: ['shimla', 'Amritsar', 'Himachal', 'Mumbai', 'Goa', 'Delhi'],
        datasets: [{
          label: 'Booking This Year',
          data: [65, 59, 80, 81, 56, 55, 40],
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)',
            'rgba(255, 159, 64, 0.2)',
            'rgba(255, 205, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(201, 203, 207, 0.2)'
          ],
          borderColor: [
            'rgb(255, 99, 132)',
            'rgb(255, 159, 64)',
            'rgb(255, 205, 86)',
            'rgb(75, 192, 192)',
            'rgb(54, 162, 235)',
            'rgb(153, 102, 255)',
            'rgb(201, 203, 207)'
          ],
          borderWidth: 2,
          pointRadius: 18
        }]
      },
      options: {
        plugins: {
          legend: {
            labels: {
              font: {
                weight: 'bold'
              },
              color: 'black' // Set legend label text color to black
            }
          }
        },
        scales: {
          x: {
            ticks: {
              color: 'black' // Set x-axis tick labels color to black
            }
          },
          y: {
            ticks: {
              color: 'black' // Set y-axis tick labels color to black
            }
          }
        }
      }
    });
  }

}
