import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';

interface Person {
  firstName: string;
  lastName: string;
  email: string;
  phone?: string;
}

interface Guest {
  firstName: string;
  lastName: string;
  email: string;
}

@Component({
  selector: 'app-destination',
  templateUrl: './destination.component.html',
  styleUrls: ['./destination.component.css']
})
export class DestinationComponent implements OnInit {
  formData: {
    mainPerson: Person;
    guests: Guest[];
  } = {
    mainPerson: {
      firstName: '',
      lastName: '',
      email: '',
      phone: ''
    },
    guests: []
  };

  files: File[] = [];
  errorMessage: string | null = null;
  
  // Maximum file size allowed (2MB in bytes)
  readonly MAX_FILE_SIZE = 2 * 1024 * 1024;

  constructor(private router: Router) {}

  ngOnInit(): void {}

  // Method to handle form submission
  submitForm(form: NgForm): void {
    if (form.valid && this.isFormValid()) {
      console.log('Form Submitted:', this.formData);
      this.router.navigate(['/form2/payment']);
      // Add navigation or further processing here
    } else {
      console.log('Form is invalid');
    }
  }

  // Method to add a new guest
  addGuest(): void {
    this.formData.guests.push({ firstName: '', lastName: '', email: '' });
  }

  // Method to remove a guest by index
  removeGuest(index: number): void {
    if (this.formData.guests.length > 1) {
      this.formData.guests.splice(index, 1);
    }
  }

  // Method to handle file selection
  onSelect(event: any) {
    this.errorMessage = null; // Reset error message

    // Iterate through the selected files
    for (const file of event.addedFiles) {
      if (file.size > this.MAX_FILE_SIZE) {
        this.errorMessage = 'File size exceeds 2MB. Please select a smaller file.';
      } else {
        this.files.push(file); // Add valid file
      }
    }
  }

  onRemove(file: File) {
    // Remove the file from the list
    this.files = this.files.filter(f => f !== file);
  }

  // Method to check if the form data is valid
  isFormValid(): boolean {
    const mainPerson = this.formData.mainPerson;
    const guests = this.formData.guests;

    // Check if main person fields are filled
    const isMainPersonValid = mainPerson.firstName.trim() !== '' &&
                              mainPerson.lastName.trim() !== '' &&
                              this.isEmailValid(mainPerson.email) &&
                              mainPerson.phone?.trim() !== '';

    // Check if all guests fields are filled
    const areGuestsValid = guests.every(guest => 
      guest.firstName.trim() !== '' &&
      guest.lastName.trim() !== '' &&
      this.isEmailValid(guest.email)
    );

    return isMainPersonValid && areGuestsValid;
  }

  // Method to validate email format
  isEmailValid(email: string): boolean {
    // Simple email regex pattern
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailPattern.test(email.trim());
  }
}
