import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { BookingFormComponent } from './booking-form/booking-form.component';
import { BlogComponent } from './blog/blog.component';
import { ContactComponent } from './contact/contact.component';
import { Form2Component } from './form2/form2.component';
import { DestinationComponent } from './form2/destination/destination.component';
import { DateComponent } from './form2/date/date.component';
import { RoomsComponent } from './form2/rooms/rooms.component';
import { HotelListComponent } from './form2/hotel-list/hotel-list.component';
import { PaymentComponent } from './form2/payment/payment.component';
import { SigninComponent } from './signin/signin.component';
import { LoginComponent } from './login/login.component';
import { TexteditorComponent } from './texteditor/texteditor.component';
import { GraphComponent } from './graph/graph.component';
import { HotelList2Component } from './form2/hotel-list2/hotel-list2.component';
import { HotelList3Component } from './form2/hotel-list3/hotel-list3.component';
import { HotelList4Component } from './form2/hotel-list4/hotel-list4.component';
import { HotelList5Component } from './form2/hotel-list5/hotel-list5.component';
import { FormReceiptComponent } from './form-receipt/form-receipt.component';
import { PdfComponent } from './form2/pdf/pdf.component';


const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'booking-form', component: BookingFormComponent },
  { path: 'blog', component: BlogComponent },
  { path: 'contact', component: ContactComponent },
  {path: 'signin', component: SigninComponent},
  {path: 'login', component: LoginComponent},
{path: "texteditor", component: TexteditorComponent},
{path: "graph", component: GraphComponent},
{path: 'form-receipt', component: FormReceiptComponent},

  {
    path: 'form2', component: Form2Component, children: [
      { path: 'date', component: DateComponent },
      { path: 'hotel_list', component: HotelListComponent },
      {path: 'hotel_list2', component: HotelList2Component},
      {path: 'hotel_list3', component: HotelList3Component},
      {path: 'hotel_list4', component: HotelList4Component},
      {path: 'hotel_list5', component: HotelList5Component},
      { path: 'destination', component: DestinationComponent },
      { path: 'rooms', component: RoomsComponent },
      { path: 'payment', component: PaymentComponent },
      {path: 'pdf', component: PdfComponent},
      { path: '', redirectTo: 'date', pathMatch: 'full' } // Default child route
    ]
  },
  { path: '**', redirectTo: '' }, // Wildcard route for 404
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
