import { Component, OnInit, ElementRef, Renderer2 } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { HeaderservicesService } from '../headerservices.service';
import { filter } from 'rxjs/operators';
import { DarkModeService } from 'angular-dark-mode';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  showHeader = true;
  darkMode$ = this.darkModeService.darkMode$;

  private hideRoutes = [
    '/booking-form',
    '/form2',
    "/login",
    "/signin",
    "/graph",
    "/texteditor",
    '/form2/destination',
    '/form2/hotel_list',
    '/form2/hotel_list2',
    '/form2/hotel_list3',
    '/form2/hotel_list4',
    '/form2/hotel_list5',
    '/form-receipt',
    '/form2/date',
    '/form2/rooms',
    '/form2/peoples',
    '/form2/payment',
    '/form2/pdf'
  ];

  constructor(
    private router: Router,
    private headerservicesService: HeaderservicesService,
    private el: ElementRef,
    private darkModeService: DarkModeService,
    private renderer: Renderer2
  ) {}

  ngOnInit(): void {
    // Subscribe to header visibility changes from the service
    this.headerservicesService.showHeader$.subscribe(show => {
      this.showHeader = show;
    });

    // Subscribe to router events to hide/show header based on the route
    this.router.events.pipe(
      filter(event => event instanceof NavigationEnd)
    ).subscribe(() => {
      const currentRoute = this.router.url;
      if (this.hideRoutes.includes(currentRoute)) {
        this.headerservicesService.hideHeader();
      } else {
        this.headerservicesService.showHeader();
      }
    });
  }

  closeNavbar() {
    const navbar = this.el.nativeElement.querySelector('#navbarSupportedContent');
    if (navbar) {
      this.renderer.setAttribute(navbar, 'class', 'collapse navbar-collapse');
    }
  }

  onToggle(): void {
    this.darkModeService.toggle();
  }
}
