import { Component } from '@angular/core';

@Component({
  selector: 'app-hotel-list3',
  templateUrl: './hotel-list3.component.html',
  styleUrl: './hotel-list3.component.css'
})
export class HotelList3Component {
  hotels: any[] = [];  // Define the hotels property here

  constructor() {}

  ngOnInit(): void {
    // Initialize hotels with data here
    this.hotels = [
      { name: 'Hotel A', location: 'Mumbai', rating: 5, price: 1000 },
      { name: 'Hotel B', location: 'Mumbai', rating: 3, price: 1200 },
      { name: 'Hotel C', location: 'Mumbai', rating: 2, price: 1500 },
      { name: 'Hotel D', location: 'Mumbai', rating: 4, price: 2000 },
      // Add more hotel data as needed
    ];
    this.hotels.forEach(hotel => {
      hotel.ratingStars = Array(hotel.rating).fill('star');  // Creates an array of 'star' strings based on rating
    });
  }
}
